<template>
    <div class="bid-warp">
        <div class="supervise">
            <header>
                <div class="bid-header-btn">
                    <van-row type="flex" align="center">
                        <van-col span="8">
                            <div :class="adressactive?'text1':'text'" @click="chooseAddress">
                                
                                <img v-show="!adressactive" src="../../assets/img/more-address.png" alt="">
                                <img v-show="adressactive" src="../../assets/img/more-address-active.png" alt="">
                            </div>
                        </van-col>
                        <van-col span="8">
                            <div class="title"></div>
                        </van-col>
                        <van-col span="8">
                            <div class="btn">
                                <img @click="tuiSong()" v-if="subscribe == 0"
                                     src="../../assets/img/xiaoxi-normal@2x.png" alt="">
                                <img @click="tuiSong()" v-else src="../../assets/img/tuisong-active.png" alt="">
                                <img v-if="!isnurseryName" @click="getMyTree" src="../../assets/img/woshu-normal@2x.png"
                                     alt="">
                                <img v-else @click="getMyTree" src="../../assets/img/woshu-active@2x.png" alt="">
                            </div>
                        </van-col>
                    </van-row>
                </div>
                <div class="bid-header">
                    <div class="input-search">
                        <input type="text" ref="searchInput" v-model="searchStr" @blur="searchIconShow"
                               @focus="searchIconToggle"
                               @keydown="getProjectByName">
                        <div :class="searchIcon?'bid-header-searchIcon':'bid-header-searchIcon2'" v-show="searchIcon"
                             @click="searchIconToggle">
                            <img src="../../assets/img/search@2x.png" alt="">
                            <span></span>
                        </div>
                    </div>
                </div>
            </header>
            <content>
                <van-list v-if="bidList.length != 0" :offset="10" @load="getMoreProject" v-model="loading"
                          :finished="finished" :finished-text="''">
                    <div class="bid-content">
                        <div class="bid-content-one" v-for="(item,index) in bidList"
                             @click="linkContract(index)" :key="item.project_id">
                            <div class="bid-content-one-title">
                                <div class="project-name" v-text="getTitle(item)">
                                </div>
                                <div class="project-name-num">
                                    <span v-text="item.tree_num+item.company"></span>
                                </div>
                                <div class="project-time">
                                    <img src="../../assets/img/daojishi-icon@2x.png" alt="">
                                    <span v-text="getlLastTime(item.project_info.Up_time)"></span>
                                </div>
                            </div>
                            <div class="bid-content-one-biaozhu">
                                <div class="bottom">
                                    <span v-if="item.dbh" v-text="''+item.dbh+''"></span>
                                    <span v-if="item.ground_diameter" v-text="''+item.ground_diameter+''"></span>
                                    <span v-if="item.plant_height" v-text="''+item.plant_height+''"></span>
                                    <span v-if="item.crown" v-text="''+item.crown+''"></span>
                                </div>
                            </div>
                            <div class="project-address-warp">
                                <img src="../../assets/img/position@2x.png" alt="">
                                <div class="project-address"
                                     v-text="item.project_info.hcity+item.project_info.hproper+item.project_info.harea">
                                </div>
                                <div class="project-name" v-text="item.project_info.partya_company_name">

                                </div>
                            </div>
                            <div class="project-address-remark" v-text="item.remarks"></div>
                        </div>
                    </div>
                </van-list>
                <div v-else>
                    <div v-if="wiff" class="supervise-none">
                        <img class="img-none" src="../../assets/img/none.png" alt="">
                        <p class="none-descript">~</p>
                    </div>
                    <div v-else class="supervise-wiff">
                        <img class="img-none" src="../../assets/img/none.png" alt="">
                        <p class="none-descript">~</p>
                        <img @click="getMoreProjectList" class="supervise-wiff-refresh"
                             src="../../assets/img/sent@2x.png" alt="">
                    </div>
                </div>
                <div v-show="adressactive" class="address-warp">
                    <div class="address-content">
                        <div :class="localAddress.length == 0?'activebtn':'btn'" @click="removeAllAddress"></div>
                        <div v-for="item in addressList" :class="addressActive(item)?'activebtn':'btn'" :key="item"
                             v-text="item" @click="changgeAddress(item)"></div>
                    </div>
                    <div class="address-btn">
                        <div @click="clearAddress"></div>
                        <div @click="confirmAddress"></div>
                    </div>
                </div>
            </content>
            <van-dialog
                    v-model="subWarpShow"
                    show-cancel-button
                    :before-close="beforeClose"
                    confirm-button-text=""
                    title=""
            >
                <div class="warp-content">
                    <van-field
                            required
                            v-model="bidInfo.tree_price"
                            label="(/)"
                            placeholder=""
                    />
                    <van-field
                            required
                            v-model="bidInfo.tree_num"
                            label="()"
                            placeholder=""
                    />
                    <div v-show="isnull_user == 0">
                        <van-field
                                required
                                v-model="bidInfo.phone"
                                label=""
                                placeholder=""
                        />
                    </div>
                    <van-field
                            @click="showChooseAddress"
                            v-model="bidInfo.tree_address"
                            required
                            clearable
                            label=""
                            icon="arrow-down"
                            @click-icon="showChooseAddress"
                            readonly
                    />
                    <van-field
                            clearable
                            label=""
                            readonly
                    />
                    <div class="imgs">

                        <van-row type="flex" justify="left" align="center">
                            <van-col span="8" v-for="(item,index) in bidInfo.tree_imgs" :key="item">
                                <div class="img-one">
                                    <img class="img-close" src="../../assets/img/cha@2x.png" alt="" @click="removeBidImg(index)">
                                    <img class="img-inner" :src="item" alt="">
                                </div>
                            </van-col>
                            <van-col span="8">
                                <label class="img-one" for="labelimg">
                                    <img class="img-btn" src="../../assets/img/shizi.png" alt="">
                                </label>
                                <van-uploader v-show="false" id="labelimg" :after-read="addimg">
                                </van-uploader>
                            </van-col>
                        </van-row>
                    </div>

                </div>
            </van-dialog>
        </div>
        <van-popup v-model="addressChooseActive" position="bottom" :overlay="false">
            <van-area :area-list="areaList" :columns-num="2" title="" @confirm="addresConfirmBack"
                      @cancel="addressChooseActive=false"/>
        </van-popup>
    </div>
</template>

<script>
    import {getBidList, getBidNurseryInfo, getMsg, getNurseryName,postBidImg,subBidInfo} from "../../api/transaction"
    import cookie from 'js-cookie'
    import {areaList} from '../../config/area'
    import {decodeImgpath} from '../../config/crypt'
    export default {
        data() {
            return {
                areaList: [],
                addressChooseActive: false,
                bidInfo: {
                    tree_price: '',
                    tree_num: '',
                    phone: '',
                    tree_imgs: [],
                    tree_id: '',
                    tree_address: ''
                },
                bidIndx: "",
                subWarpShow: false,
                isnull_user: 0,
                nurseryName: [],
                loading: false,
                finished: false,
                searchStr: "",
                searchIcon: true,
                adressactive: false,
                page: 0,
                bidList: [],
                wiff: true,
                subscribe: 0,
                isnurseryName: false,
                treeInfo: {
                    dbh: "222gp"
                },
                localAddress: [],
                addressList: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
                geturlid: "",
                imgArr: "",
            }
        },
        created() {
            this.geturlid = this.urlGetfun('project_id');
            this.areaList = areaList;
            let localAddress = cookie.get('address', this.localAddress)
            localAddress ? this.localAddress = JSON.parse(cookie.get('address', this.localAddress)) : this.localAddress = []
            this.getMoreProjectList()
            getNurseryName({}).then(res => {
                if (res.data.data) {
                    this.nurseryName = res.data.data
                    this.isnull_user = res.data.isnull_user
                }
            })
        },
        methods: {
        	//
            urlGetfun(what) {
				var aQuery = window.location.href.split("?"); //Get
				var aGET = new Array();
				if(aQuery.length > 1) {
					var aBuf = aQuery[1].split("&");
					for(var i = 0, iLoop = aBuf.length; i < iLoop; i++) {
						var aTmp = aBuf[i].split("="); //keyValue
						aGET[aTmp[0]] = aTmp[1];
                    }
                    return aGET[what];
				}else{
                    return "";
                }
			},
            //index
            removeBidImg(index){
                var tree_imgs = this.bidInfo.tree_imgs;
                tree_imgs.splice(index,1)
                this.$set(this.bidInfo,"tree_imgs",tree_imgs)
            },
            //
            addimg(e){
                let file = e.file;
                let self = this;
                let Orientation;
                //
                this.EXIF.getData(file, function(){
                    Orientation = self.EXIF.getTag(this, 'Orientation');
                });
                // FileReader 
                if (!file || !window.FileReader) return;
                
                if (/^image/.test(file.type)) {
                    // reader
                    let reader = new FileReader();
                    // 2 base64 
                    reader.readAsDataURL(file);
                    // 
                    reader.onloadend = function () {
                        let result = this.result;
                        let img = new Image();
                        img.src = result;
                        //100K,
                        if (this.result.length <= (100 * 1024)) {
                            self.postImg(file);
                        }else {
                            img.onload = function () {
                                let data = self.compress(img,Orientation);
                                let file = self.dataURItoBlob(data);
                                self.postImg(file);
                            }
                        }
                    }
                }  
            },
            postImg (file) {
                //
                var formData=new FormData();
                formData.append('photo', file);
                postBidImg(formData).then(res=>{
                    if(res.data.status == 0){
                        var tree_imgs = this.bidInfo.tree_imgs
                        tree_imgs.push(res.data.photo)
                        this.$set(this.bidInfo,"tree_imgs",tree_imgs)
                    }else {
                        this.Notify({
                            message: res.data.msg,
                            duration: 1000,
                            background: '#BA4E4E'
                        });
                    }
                })
            },
            rotateImg (img, direction,canvas) {
                //4
                const min_step = 0;
                const max_step = 3;
                if (img == null)return;
                //imgimg
                let height = img.height;
                let width = img.width;
                let step = 2;
                if (step == null) {
                step = min_step;
                }
                if (direction == 'right') {
                step++;
                //
                step > max_step && (step = min_step);
                } else {
                step--;
                step < min_step && (step = max_step);
                }
                //
                let degree = step * 90 * Math.PI / 180;
                let ctx = canvas.getContext('2d');
                switch (step) {
                case 0:
                canvas.width = width;
                canvas.height = height;
                ctx.drawImage(img, 0, 0);
                break;
                case 1:
                canvas.width = height;
                canvas.height = width;
                ctx.rotate(degree);
                ctx.drawImage(img, 0, -height);
                break;
                case 2:
                canvas.width = width;
                canvas.height = height;
                ctx.rotate(degree);
                ctx.drawImage(img, -width, -height);
                break;
                case 3:
                canvas.width = height;
                canvas.height = width;
                ctx.rotate(degree);
                ctx.drawImage(img, -width, 0);
                break;
                }
            },
            compress(img,Orientation) {
                let canvas = document.createElement("canvas");
                let ctx = canvas.getContext('2d');
                    //canvas
                let tCanvas = document.createElement("canvas");
                let tctx = tCanvas.getContext("2d");
                let initSize = img.src.length;
                let width = img.width;
                let height = img.height;
                //400
                let ratio;
                if ((ratio = width * height / 4000000) > 1) {
                    console.log("400")
                    ratio = Math.sqrt(ratio);
                    width /= ratio;
                    height /= ratio;
                } else {
                    ratio = 1;
                }
                canvas.width = width;
                canvas.height = height;
                //    
                ctx.fillStyle = "#fff";
                ctx.fillRect(0, 0, canvas.width, canvas.height);
                //100
                let count;
                if ((count = width * height / 1000000) > 1) {
                    console.log("100W");
                    count = ~~(Math.sqrt(count) + 1); //
                //      
                    let nw = ~~(width / count);
                    let nh = ~~(height / count);
                    tCanvas.width = nw;
                    tCanvas.height = nh;
                    for (let i = 0; i < count; i++) {
                    for (let j = 0; j < count; j++) {
                    tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
                    ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
                    }
                    }
                } else {
                    ctx.drawImage(img, 0, 0, width, height);
                }
                //ios 
                if(Orientation != "" && Orientation != 1){
                    switch(Orientation){
                    case 6://90
                    this.rotateImg(img,'left',canvas);
                    break;
                    case 8://90
                    this.rotateImg(img,'right',canvas);
                    break;
                    case 3://180
                    this.rotateImg(img,'right',canvas);//
                    this.rotateImg(img,'right',canvas);
                    break;
                    }
                }
                //
                let ndata = canvas.toDataURL('image/jpeg', 0.1);
                console.log('' + initSize);
                console.log('' + ndata.length);
                console.log('' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
                tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
                return ndata;
            },
            dataURItoBlob(base64Data) {
                var byteString;
                if (base64Data.split(",")[0].indexOf("base64") >= 0)
                    byteString = atob(base64Data.split(",")[1]);
                else byteString = unescape(base64Data.split(",")[1]);
                var mimeString = base64Data
                    .split(",")[0]
                    .split(":")[1]
                    .split(";")[0];
                var ia = new Uint8Array(byteString.length);
                for (var i = 0; i < byteString.length; i++) {
                    ia[i] = byteString.charCodeAt(i);
                }
                return new Blob([ia], {type: mimeString});
            },
            //
            addresConfirmBack(back) {
                var address = "";
                address += back[0].name
                address += " "
                address += back[1].name
                this.bidInfo.tree_address = address
                this.addressChooseActive = false
            },
            //
            showChooseAddress() {
                this.addressChooseActive = true
            },
            //
            clearBidInfo() {
                this.bidInfo = {
                    tree_price: '',
                    tree_num: '',
                    phone: '',
                    tree_imgs: [],
                    tree_id: '',
                    tree_address: ''
                }
                this.addressChooseActive = false
            },
            //
            beforeClose(action, done) {
                if (action == "cancel") {
                    this.clearBidInfo()
                    done()
                } else {
                    let data = this.bidInfo
                    if(this.bidInfo.tree_price == ""){
                        this.Notify({
                            message: "",
                            duration: 1000,
                            background: '#BA4E4E'
                        });
                        done(false)
                        return
                    }
                    if(this.bidInfo.tree_num == ""){
                        this.Notify({
                            message: "",
                            duration: 1000,
                            background: '#BA4E4E'
                        });
                        done(false)
                        return
                    }
                    if(this.bidInfo.tree_address == ""){
                        this.Notify({
                            message: "",
                            duration: 1000,
                            background: '#BA4E4E'
                        });
                        done(false)
                        return
                    }
                    if(this.bidInfo.phone == "" && this.isnull_user == 0){
                        this.Notify({
                            message: "",
                            duration: 1000,
                            background: '#BA4E4E'
                        });
                        done(false)
                        return
                    }
                    data.tree_id = this.bidList[this.bidIndx].tree_order_id
                    subBidInfo(data).then(() =>{
                        this.Notify({
                            message: '',
                            duration: 2000,
                            background: '#4EACBA'
                        });
                        var bidlist = this.bidList
                        bidlist.splice(this.bidIndx,1)
                        this.bidList = bidlist
                        done()
                        this.clearBidInfo()
                    }).catch(() =>{
                        this.Notify({
                            message: "!",
                            duration: 1000,
                            background: '#BA4E4E'
                        });
                        done()
                        this.clearBidInfo()
                    })
                }
            },
            //
            removeAllAddress() {
                this.localAddress = []
            },
            //
            addressActive(name) {
                return this.localAddress.indexOf(name) != -1
            },
            //
            changgeAddress(name) {
                var arr = this.localAddress
                if (this.localAddress.indexOf(name) > -1) {
                    arr.splice(this.localAddress.indexOf(name), 1)
                } else {
                    arr.push(name)
                }
                this.localAddress = arr
            },
            //
            confirmAddress() {
                this.adressactive = false;
                this.page = 0;
                cookie.set('address', this.localAddress)
                this.getMoreProjectList()
            },
            //
            clearAddress() {
                this.localAddress = [];
                this.adressactive = false;
                this.page = 0;
                this.getMoreProjectList()
            },
            //
            chooseAddress() {
                this.adressactive = !this.adressactive;
            },
            //
            tuiSong() {
                let data = {}
                if (this.subscribe == 0) {
                    data.get_msg = 1
                } else {
                    data.get_msg = 0
                }
                data.userid = JSON.parse(cookie.get('user2')).userid
                getMsg(data).then(() => {   
                    if (this.subscribe == 0) {
                        this.subscribe = 1
                        this.Notify({
                            message: '',
                            duration: 2000,
                            background: '#4EACBA'
                        });
                    } else {
                        this.subscribe = 0
                        this.Notify({
                            message: "",
                            duration: 1000,
                            background: '#BA4E4E'
                        });
                    }
                })
            },
            getMyTree() {
                this.isnurseryName = true;
                this.page = 0;
                this.getMoreProjectList()
            },
            getMoreProject() {
                this.getMoreProjectList()
            },
            //
            imgsPush(path) {
                let imgs = this.bidInfo.tree_imgs
                imgs.push(path)
                this.$set(this.bidInfo, "tree_imgs", imgs)
            },
            //
            imgsRemove(index) {
                let imgs = this.bidInfo.tree_imgs
                imgs.slice(index, 1)
                this.$set(this.bidInfo, "tree_imgs", imgs)
            },
            // 
            linkContract(index) {
                this.bidIndx = index;
                let data = {
                    tree_id: this.bidList[index].tree_order_id,
                    tree_name: this.bidList[index].tree_name
                }
                getBidNurseryInfo(data).then(res => {
                    let info = res.data.nursery
                    this.bidInfo = {
                        tree_price: info.price ? info.price : "",
                        tree_num: info.count ? info.count : "",
                        phone: '',
                        tree_imgs: [],
                        tree_address: info.province ? info.province : ""
                    }
                    if (info.province) {
                        this.bidInfo.tree_address = info.province + " " + info.city
                    }
                    if (info.imgpath) {
                        this.imgArr = decodeImgpath(info.imgpath);
                        var imgArray = this.imgArr
                        console.log(this.imgArr);
                        for(var i = 0, iLoop = imgArray.length; i < iLoop; i++) {
                            this.imgsPush("http://cnzhaoshu.com/trees/b2/"+imgArray[i])
                        }
                        
                    }
                    this.subWarpShow = true;
                }).catch(() => {
                    this.subWarpShow = true;
                })
            },
            searchIconToggle() {
                this.searchIcon = false;
                this.$refs["searchInput"].focus();
            },
            searchIconShow() {
                if (this.searchStr == "") {
                    this.searchIcon = true
                }
            },
            //
            getlLastTime(Up_time) {
                var time = new Date().getTime();
                var endtime = new Date(Up_time.replace(/-/g, "/")).getTime();
                var timenum = (endtime - time) / 1000 / 60 / 60;
                var timestr = "";
                if (time <= 0) {
                    timestr = ""
                } else {
                    if (parseInt(timenum) < 1) {
                        timestr = "1"

                    } else if (1 < parseInt(timenum) && parseInt(timenum) < 24) {
                        timestr = "" + parseInt(timenum) + ""

                    } else {
                        timestr = "" + parseInt(parseInt(timenum) / 24) + ""
                    }
                }
                return timestr
            },
            getTitle(treeInfo) {
                var title = "";
                if (treeInfo.dbh) {
                    title = treeInfo.dbh + "";
                }else if (treeInfo.ground_diameter) {
                    title = treeInfo.ground_diameter + ""
                }else if (treeInfo.plant_height) {
                    title = treeInfo.plant_height + ""
                } else if (treeInfo.crown) {
                    title = treeInfo.crown + ""
                }
                title += treeInfo.tree_name
                return title
            },
            //
            getProjectByName(e) {
                if (e.keyCode == 13) {
                    this.page = 0;
                    this.isnurseryName = false;
                    this.Toast.loading({
                        message: '...',
                        duration: 0
                    });
                    this.getMoreProjectList()
                }
            },
            //  
            myTreeAllStr() {
                let miaoputreeList = this.nurseryName;
                let arrstr = ""
                for (var index in miaoputreeList) {
                    if (miaoputreeList[index].dbh) {
                        arrstr += miaoputreeList[index].dbh + "";
                    } else if (miaoputreeList[index].height) {
                        arrstr += miaoputreeList[index].height + ""
                    } else if (miaoputreeList[index].crownwidth) {
                        arrstr += miaoputreeList[index].crownwidth + ""
                    }
                    arrstr += `${miaoputreeList[index].name}`
                }
                return arrstr += ""
            },
            getList(data) {
                getBidList(data).then(res => {
                    this.Toast.clear()
                    var bidList = []
                    this.subscribe = res.data.subscribe
                    if (res.data.p == 1) {
                        bidList = res.data.data
                    } else {
                        bidList = this.bidList.concat(res.data.data)
                    }
                    this.$set(this, "bidList", bidList)
                    if (res.data.data.length == 0) {
                        this.finished = true
                    }
                    this.loading = false;

                }).catch(() => {
                    this.Toast.clear()
                    this.wiff = false;
                    this.page = 0
                })
            },
            getMoreProjectList() {
                this.page = this.page + 1;
                if(this.page == 1){
                    this.finished = false
                }
                let data = {} 
                if (!this.isnurseryName) {
                    data = {
                        name: this.searchStr,
                        p: this.page,
                        address: this.localAddress,
                        project_id:this.geturlid
                    }
                    this.Toast.loading({
                        message: '...',
                        duration: 0
                    });
                    this.getList(data)

                } else {
                    data = {
                        nursery_info: this.nurseryName,
                        p: this.page,
                        address: this.localAddress
                    }
                    if (this.nurseryName.length == 0) {
                        this.$toast(',!');
                        return
                    }
                    this.Dialog.alert({
                        title: '',
                        message: this.myTreeAllStr()
                    }).then(() => {
                        this.Toast.loading({
                            message: '...',
                            duration: 0
                        });
                        this.getList(data)
                    });
                }

            }
        }
    };
</script>

<style lang="less">
    .supervise {
        .warp-content {
            padding-top: 16px;
            .imgs {
                padding: 0 0.4rem 0.26667rem 0.4rem;
                .van-row--align-center{
                    flex-wrap: wrap !important;
                    .van-col{
                        margin-top: 10px;
                    }
                }
